export default {
	data() {
		return {
			imgUrl:''
		}
	},
	onLoad() {

	},
	onShow(){
		this.getImg()
		this.login()
	},
	methods: {
		login(){
			let that = this;
			wx.login({
				success(res) {
					console.log(res.code)
					console.log(typeof res.code)
					uni.request({
						url:that.api+'/Api/Login/Login?code='+res.code,
						method:'GET',
						success:function(res){
							console.log(res)
							console.log('请求成功')
						},
						fail:function(err){
							console.log(err)
							console.log('请求')
						}
					})
				}
			})
		},
		// 请求图片
		getImg(){
			let that = this;
			uni.request({
				url:that.api+'/Api/Index/getImg',
				dataType:'json',
				data:{
					
				},
				method:'POST',
				success:function(res){
					if(res.data.status == 100000){
						that.imgUrl = res.data.info.img_url
					}else{
						uni.showToast({ title: res.data.msg, icon: "none" });
					}
				},
				fail:function(err){
					console.log(err)
				}
			})
		},
		changePic(){
			this.getImg()
		}
	}
}
